
*{
  font-family: 'Verdana', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.title_container {
  text-align: center;
  margin-top: 5vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.container{
  text-align: center;
  margin-top: 17vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
select {
  margin: 0 10px;
}
.source-text-container, .target-text-container{
  margin: 0 20px;
  padding: 5px;
  width: 80vh;
  height: 35vh;
  border: 1px solid #2c3e50;
}
@media only screen and (max-width: 800px) {
.container {
    margin-top:1%;
}
#selectedSourceLanguage{
    width: 90%;
    margin: 0.5%;
}
#selectedTargetLanguage {
    width: 90%;
}
.source-text-container {
    width: 90%;
    margin: 0 2% 2% 2%;
}
.target-text-container {
    width: 90%;
    margin: 0 2% 2% 2%;
}
}
